.block.selected, .block.interactive:active {
  -webkit-box-shadow: inset 0 0 0 1px #333, inset 0 0 0 7px rgba(0, 0, 0, 0.33);
          box-shadow: inset 0 0 0 1px #333, inset 0 0 0 7px rgba(0, 0, 0, 0.33);
}
.block.color-0 {
  background-color: #444;
}
.block.interactive {
  cursor: pointer;
  background-color: #999;
}
.block.color-1 {
  background-color: #bf7!important;
}
.block.color-2 {
  background-color: #f7b!important;
}
.block.color-3 {
  background-color: #7bf!important;
}
.block {
  -webkit-box-shadow: inset 0 0 0 1px #333;
          box-shadow: inset 0 0 0 1px #333;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.block.interactive:hover {
  outline: 1px dotted black;
  outline-offset: -3px;
}
.block.interactive:active {
  outline: 2px solid black;
  outline-offset: -3px;
}
.block.inactive {
  opacity: 0.2;
}


